import React from 'react';
import { NavLink } from 'react-router-dom';



class MenuPage extends React.Component {

    render() {

        return (
            <React.Fragment>
                <NavLink to="/" exact className="PageLink" activeClassName="ActivePageLink">  </NavLink>
                <NavLink to="/about" className="PageLink" activeClassName="ActivePageLink"> </NavLink>
            </React.Fragment>
        );

    }

}

export default MenuPage;




